import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import axios from "axios";
import moment from "moment";

import "./css/Memory.css";
const Memory = () => {
    const { _id } = useParams();
    const images_url = useSelector((state) => state.urls.uploads);
    const memories_url = useSelector((state) => state.urls.memories);

    const [memory, setMemory] = useState({
        creator: "",
        title: "",
        description: "",
        tags: [],
        created_at: "",
        updated_at: "",
        image: "",
    });
    //     const [memories, setMemories] = useState([]);

    useEffect(() => {
        axios
            .get(memories_url + _id)
            .then((res) => {
                setMemory({
                    creator: res.data.creator,
                    title: res.data.title,
                    description: res.data.description,
                    tags: res.data.tags,
                    created_at: res.data.created_at,
                    updated_at: res.data.updated_at,
                    image: res.data.image,
                });
            })
            .catch((err) => {
                console.log(err);
                console.log(err.response);
            });
    }, [memories_url, _id]);

    //     useEffect(() => {
    //         axios.get(memories_url).then((res) => {
    //             setMemories(res.data.memories);
    //         });
    //     }, [memories_url]);

    return (
        <>
            <div className="memory normal-grid justify-content-between rounded-top custom-shadow bg-white p-3">
                <div className="text">
                    <h1 className="display-3 lead-400 text-capitalize m-0">
                        {memory.title}
                    </h1>
                    <p className="lead">
                        {memory.tags.map((tag) => `#${tag} `)}
                    </p>
                    <p>{memory.description}</p>

                    <h5>creator: {memory.creator}</h5>
                    <p className="m-0">
                        created at: {moment(memory.created_at).fromNow()}
                    </p>
                    <p className="m-0">
                        last update was at:{" "}
                        {moment(memory.updated_at).fromNow()}
                    </p>

                    <hr />
                    <h6>comments section... Soon!</h6>
                </div>
                <div className="img">
                    <img
                        src={images_url + memory.image}
                        alt="img"
                        className="rounded w-100"
                    />
                </div>
            </div>
            {/* 
            <div className="suggestions rounded-bottom custom-shadow bg-white mt-3 p-3">
                <h4 className="m-0">You might also like</h4>
                <div className="grid-300">
                    {memories.map((memory) => {
                        return (
                            <Post
                                key={memory._id}
                                _id={memory._id}
                                image={memory.image}
                                creator={memory.creator}
                                creatorEmail={memory.creatorEmail}
                                updated_at={memory.updated_at}
                                tags={memory.tags}
                                title={memory.title}
                                description={memory.description}
                                likes={memory.likes}
                            />
                        );
                    })}
                </div>
            </div> */}
        </>
    );
};

export default Memory;
